@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

*{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   max-height: 100vh;
   font-family: 'Inter', sans-serif;
   user-select: none;
}

body{

   & #root{
      width: 100%;
      height: 100vh;

      & #container{
         width: 100%;
         height: 100%;
         display: flex;
         flex-direction: column;
         justify-content: space-evenly;
         align-items: center;

         & .card{
            width: 28%;
            width: 400px;
            height: 80%;
            border-radius: 1vw;

            & .empty{
               width: 100%;
               height: 100%;
               display: grid;
               overflow: hidden;
               display: flex;
               text-align: center;
               align-items: center;
               justify-content: center;

               & h1{
                  font-size: 3.25rem;
                  font-weight: 400;
                  color: rgb(185, 185, 185);
               }

               & img{
                  width: 40%;
                  transition: 2s;
                  position: relative;
                  top: 5vw;
                  transition: 1s;
                  animation-name: bounce;
                  animation-iteration-count: infinite;
                  animation-fill-mode: both;
                  animation-play-state: running;
                  animation-duration: 4s;
                  background-color: transparent;
                  animation-delay: 3s;
                
                  
               }
            }

            & .able{
               padding: 3%;
               box-sizing: border-box;
               height: 100%;
               width: 27.5%;
               min-width: 400px;
               

               & h2{
                  text-align: center;
                  font-size: 1.25rem;
                  font-weight: 400;
               }

               & .main{
                  width: 100%;
                  height: 20%;
                  margin-top: 10%;
                  display: flex;
                  justify-content: space-evenly;
                  align-items: center;
              

                  & .left{
                     width: 40%;
                     height: 100%;
                     box-shadow: 0 0 0.5vw 0.1vw rgb(211, 211, 211);
                     border-radius: 0.5vw;
                     display: grid;
                     place-items: center;
                     color: rgb(28, 27, 116);
                     cursor: pointer;
                     
                     & h2{
                        font-weight: 400;
                        font-size: 1.75rem;
                     }
                  }

                  & .right{
                     width: 40%;
                     display: flex;
                     flex-direction: column;
                     align-items: center;

                     & h2{
                        font-weight: 500;
                        font-size: 0.9rem;
                     }
                  }
               }

               & .city{
                  text-align: center;
                  margin-top: 10%;
                  
               }

               & .city{
                  font-size: 1.5rem;
                  font-weight: 500;
                  color: rgb(255, 128, 128);
                  letter-spacing: 0.25vw;
                  margin-bottom: 7.5%;
               }

               & .info{
                  width: 100%;
                  height: 45%;
                  border-radius: 1vw;
                  display: grid;
                  grid-template-columns: 0.8fr 0.8fr;
                  display: grid;
                  place-items: center;
                  margin-top: 10%;

                  & .a {
                     background-color: rgb(235, 255, 218);
                     width: 90%;
                     height: 75%;
                     // display: grid;
                     // place-items: center;
                     display: flex;
                     align-items: center;
                     flex-direction: column;
                     border-radius: 0.5vw;
                     padding-top: 4%;
               

                     & img{
                        width: 20%;
                     }

                     & .title{
                        font-weight: 500;
                        color: gray;
                     }

                     & .bottom{
                        width: 100%;
                        height: 90%;
                        display: flex;
                        align-items: center;
                        justify-content: space-evenly;
                        color: rgb(99, 99, 236);
                        font-size: 1.25rem;
                        font-weight: 500;
                     }

                     
                  }

               }
               
            }
         }
         & .search{
            width: 27.5%;
            min-width: 400px;
            
            height: 10%;
            display: grid;
            place-items: center;
            background-color: transparent;
            border-radius: 0.6vw;

            & input{
               width: 100%;
               height: 75%;
               outline: none;
               border: none;
               padding-left: 5%;
               background-color: rgb(255, 255, 255);
               border-radius: 0.5vw;
               font-size: 1.25rem;
               font-weight: 500;
               color: rgb(1, 1, 22);
               letter-spacing: 0.05vw;
            }
         }
         & .location{
            width: 20%;
            height: 8%;
            position: absolute;
            left: 78%;
            top: 86%;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;

            & h2{
               font-size: 1.5rem;
               text-align: right;
            }
            & h3{
               font-size: 1rem;
               text-align: right;
            }
      }
      }

      & .light{
            animation-name: lights;
            animation-iteration-count: infinite;
            animation-duration: 60s;
            animation-timing-function: linear;

            & .card{
               background-color: white;
               box-shadow: 0 0 0.5vw 0.1vw rgb(209, 204, 204);
            }
            
         }

      

   }

}

@keyframes bounce{
   0%{
      top: 0vw;
   }50%{
      top: -5vw;
   }100%{
      top: 0vw;
   }
}

@keyframes lights{
   0%{
      background-color: rgb(220, 255, 220);
   }25%{
      background-color: rgb(255, 213, 213);
   }50%{
      background-color: rgb(195, 195, 255);
   }75%{
      background-color: aliceblue;
   }100%{
      background-color: rgb(255, 255, 202);
   }
}

@media all and(max-width:800px){

   body{

      & #root{

         & #container{

            & .location{
               display: none;
            }

            & .search{
               width: 80%;
               min-width: 80%;
            }
         }
      }
   }
}

@media all and(max-width:500px){

   body{

      & #root{

         & #container{
            
            & .card{
               width: 80%;
               

               & .able{
                  width: 100%;
                  min-width: fit-content;
               }
            }
         }
      }
   }
}