@-webkit-keyframes bounce {
  0% {
    top: 0vw;
  }
  50% {
    top: -5vw;
  }
  100% {
    top: 0vw;
  }
}

@keyframes bounce {
  0% {
    top: 0vw;
  }
  50% {
    top: -5vw;
  }
  100% {
    top: 0vw;
  }
}

@-webkit-keyframes lights {
  0% {
    background-color: #dcffdc;
  }
  25% {
    background-color: #ffd5d5;
  }
  50% {
    background-color: #c3c3ff;
  }
  75% {
    background-color: aliceblue;
  }
  100% {
    background-color: #ffffca;
  }
}

